import React from 'react';
import logo from './logo.svg';
import './app.scss';
import {CSSTransition, TransitionGroup} from 'react-transition-group';


const list = [
  { dayName: 'Monday', temp: '10', wind: '5', weather: 'clear' },
  { dayName: 'Tuesday', temp: '10', wind: '5', weather: 'clear' },
  { dayName: 'Wednesday', temp: '10', wind: '5', weather: 'clear' },
  { dayName: 'Thursday', temp: '10', wind: '5', weather: 'clear' },
  { dayName: 'Friday', temp: '10', wind: '5', weather: 'clear' },
  { dayName: 'Saturday', temp: '10', wind: '5', weather: 'clear' },
  { dayName: 'Sunday', temp: '10', wind: '5', weather: 'clear' },
];

class Item extends React.Component {
  state = {
    full: false,
  }

  handleClick = () => {
    this.setState({full: !this.state.full})
  }

  render() {
    const { full } = this.state;
    const { item } = this.props;
    return (
      <div className = "forecast__item">
      
        <CSSTransition
          in = { !full }
          appear = { false }
          timeout = { 300 }
          unmountOnExit
          classNames = "row"
        >
          <div
            className = "forecast__row"
            onClick = { this.handleClick }
          >
            <div className="forecast__day">{item.dayName}</div>
            <div className="forecast__weather">{item.weather}</div>
            <div className="forecast__weather">{item.weather}</div>
            <div className="forecast__temp">{item.temp}</div>
            <div className="forecast__wind">{item.wind}</div>
          </div>
        </CSSTransition>
      
        <CSSTransition
          in = { full }
          appear = { false }
          timeout = { 300 }
          unmountOnExit
          classNames = "row-full"
        >
          <div
            className = "forecast__row-full"
            onClick = { this.handleClick }
          ></div>
        </CSSTransition>

      </div>
    )
  }
}


class App extends React.Component{
  state = {
    itemFull: 'fact',
  }

  render() {
    return (
      <div className="app">
        <div className="forecast">
          <div className="fact">
          </div>
          {list.map((item) => 
            <Item item={item}/>
          )}
        </div>
      </div>
    )
  }
}

export default App;
